<template lang="pug">
    .departments__content
        .departments__title Департаменты
        .departments__container
            ul.departments__list
                swiper.swiper(:options="swiperOption")
                    swiper-slide(v-for='item in slidesNumber')
                        li.departments__item(v-for="department, idx in departments" :key="department.id"  v-if='(item-1)*steps <= idx && idx< item*steps')
                            department-item(
                                :department='department'
                            )
                    .swiper-pagination.swiper-pagination-bullets(slot="pagination")
        button(@click.prevent='addDepartment' id='addDepartment').add__department Добавить департамент
        add-department-form(
                v-if='addDepartmentFormVisible'
                v-on:eventNewDepartment='eventNewDepartment'
            )

</template>
<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'
import { mapState, mapActions } from "vuex";
export default {
    components: {
        departmentItem: () => import("../../admin/departments/department-item"),
        addDepartmentForm: () => import("../../admin/departments/add-department-form"),
        Swiper,
        SwiperSlide
    },
    created() {
        this.fetchDepartments()
    },
    computed: {
        ...mapState ("departments", {
            departments: state => state.departments
        }),
        slidesNumber: function(){
            return (Math.floor(this.departments.length/this.steps)==this.departments.length/this.steps)?Math.floor(this.departments.length/this.steps):Math.floor(this.departments.length/this.steps)+1
        }
    },
    data () {
        return {
            steps: 5,
            swiperOption: {
                pagination: {
                    el: '.swiper-pagination',
                    clickable: true,
                    renderBullet(index, className) {
                    return `<span class="${className} swiper-pagination-bullet-custom">${index + 1}</span>`
                    }
                }
            },
            addDepartmentFormVisible: false
        }
    },
    methods: {
        ...mapActions("departments", ["fetchDepartments"]),
        addDepartment() {
            this.addDepartmentFormVisible = !this.addDepartmentFormVisible 
            document.getElementById('addDepartment').setAttribute("disabled", "true")
        },
        eventNewDepartment() {
            this.addDepartmentFormVisible=!this.addDepartmentFormVisible
            document.getElementById('addDepartment').removeAttribute("disabled")
      }
    }
}
</script>
